body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #2b2020;
  min-height: 100vh; }

.nice-alert {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 40vmin;
  min-height: 200px;
  width: 60vmin;
  min-width: 300px;
  border-radius: 50%;
  overflow: hidden;
  -webkit-transform: rotate(-360deg) scale(0.4, 0.6);
          transform: rotate(-360deg) scale(0.4, 0.6);
  background: white;
  -webkit-animation: nice-alert-start 1s ease forwards, nice-alert-normalize 3s 3.3s ease forwards;
          animation: nice-alert-start 1s ease forwards, nice-alert-normalize 3s 3.3s ease forwards;
  font-size: calc(10px + 2vmin); }
  .nice-alert::after, .nice-alert::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-transform: scale(0);
            transform: scale(0); }
  .nice-alert::before {
    z-index: 1;
    background-color: red;
    -webkit-animation: nice-alert-layer 8s 1s ease forwards;
            animation: nice-alert-layer 8s 1s ease forwards; }
  .nice-alert::after {
    z-index: 2;
    background-color: green;
    /*delay 1.3s*/
    -webkit-animation: nice-alert-layer 8s 1.3s ease forwards;
            animation: nice-alert-layer 8s 1.3s ease forwards; }
  .nice-alert .nice-alert-inner {
    opacity: 0;
    -webkit-animation: nice-alert-inner-show .3s 5.3s ease forwards;
            animation: nice-alert-inner-show .3s 5.3s ease forwards; }

.App-header {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white; }

@-webkit-keyframes nice-alert-normalize {
  10% {
    border-radius: 25%;
    -webkit-transform: rotate(-360deg) scale(0.4, 0.6);
            transform: rotate(-360deg) scale(0.4, 0.6); }
  70% {
    border-radius: 25%;
    -webkit-transform: rotate(0) scale(0.4, 0.6);
            transform: rotate(0) scale(0.4, 0.6); }
  90% {
    border-radius: 25%;
    -webkit-transform: rotate(0) scale(0.4, 0.6);
            transform: rotate(0) scale(0.4, 0.6); }
  95% {
    border-radius: 10px;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 10px; } }

@keyframes nice-alert-normalize {
  10% {
    border-radius: 25%;
    -webkit-transform: rotate(-360deg) scale(0.4, 0.6);
            transform: rotate(-360deg) scale(0.4, 0.6); }
  70% {
