            transform: rotate(0) scale(0.4, 0.6); }
  95% {
    border-radius: 10px;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 10px; } }

@-webkit-keyframes nice-alert-inner-show {
  100% {
    opacity: 1; } }

@keyframes nice-alert-inner-show {
