    opacity: 1; } }

@-webkit-keyframes nice-alert-layer {
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 1; }
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0; } }

@keyframes nice-alert-layer {
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 1; }
